/* General Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

.container {
  width: 80%;
  margin: 0 auto;
}

h2 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 20px;
}

h3 {
  font-size: 1.8rem;
  color: #333;
}

section {
  padding: 60px 0;
}

/* Navbar */
.navbar {
  background-color: #333;
  padding: 20px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}

.navbar div{
  display: flex;
  gap: 50%;
  justify-content: space-between;
  align-items: center;
  animation: slideBottom 1s ease forwards;
  animation-delay: 1s;
}
.navbar h1{
  margin-right: 50px;
}
.navbar a:hover{
  color: wheat;
  
}

.navbar h1 {
  color: #fff;
  font-size: 2rem;
  text-align: center;
}

.navbar ul {
  display: flex;
  justify-content: center;
  list-style: none;
  margin-top: 10px;
}

.navbar ul li {
  margin: 0 20px;
}

.navbar ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 1.2rem;
}

/* About Section */
.about {
  background-color: #3905f5;
  color: wheat;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
  margin-top: 5%;
  padding: 40px;
}
.a-container{
  animation: slideRight 1s ease forwards;
  animation-delay: 1.5s;
}
.bio h2 {
  color: white;
}
.bio span{
  color: #61DBFB;
}

.profile-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}

.bio {
  flex: 1;
  margin-left: 30px;
}

/* Skills Section */
.skills {
  background-color: #e7e7e7;
}
#skills div h1{
  font-size: 40px;
}
#skills div span{
   color: #0f0bf3;
}
.s-container{
  animation: slideLeft 1s ease forwards;
  animation-delay: 2s;
}
.service-list{
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(259px,1fr));
  grid-gap: 40px;
  margin-top: 50px;
}
.service-list div{
  background: transparent;
  padding: 40px;
  font-size: 13px;
  font-weight: 13px;
  /* border-right: 10px; */
  border-radius: 20px;
  /* transition: background 0.5s,transform 0.5s; */
  box-shadow: 1px 1px 20px blue, 1px 1px 40px lightblue;
}
.service-list div:hover{
  transform: translateY(-10px);
}
.service-list div i{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 200px;
  margin-bottom: 30px;
}
.service-list div a{
  text-decoration: none;
  /* color: #081b29; */
  display: inline-block;
  font-size: 12px;
  margin-top: 20px;
}
.skills-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.skill {
  width: 22%;
  text-align: center;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.progress-bar {
  width: 100%;
  height: 12px;
  background-color: #ddd;
  border-radius: 20px;
  margin-bottom: 10px;
}

.progress {
  height: 100%;
  width: 0%;
  border-radius: 20px;
  transition: width 1s ease-in-out;
}

.html { background-color: #f06529; }
.css { background-color: #2965f1; }
.js { background-color: #f0db4f; }
.react { background-color: #61DBFB; }

/* Projects Section */
.projects {
  background-color:skyblue;
}
.p-ccontainer{
  animation: slideTop 1s ease forwards;
  animation-delay: 2s;
}

.projects-container {
  display: flex;
  justify-content: space-between;
  gap: 30px;
}

.project {
  width: 50%;
  background: #f4f4f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  height: 350px;

}

.project img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
}

.btn {
  text-decoration: none;
  background-color: #333;
  color: #fff;
  padding: 10px;
  margin-top:30px ;
  border-radius: 5px;
  
}
/* Resume Section */

.resume{
 background-color: rgb(51, 177, 219); 
}
.resume h2{
  margin-left: 30px;
}
.resume-container{
    display: flex;
    gap: 50px;
    place-content: center;
    animation: slideRight 1s ease forwards;
  animation-delay: 2s;
}
.resume-info{
  display: grid;
  place-content: center;
  font-size: 20px;
}
.resume a:hover{
   color: white;
}

/* Contact Section */
.contact {
  display: grid;
  place-content: center;
  
  color: #f0f0f0;
  background: rgb(32, 62, 235);
}
.contact-words{
  font-size: 20px;
}
.contact-info{
    font-size: 20px;
    font-weight: bold;
}

.contact-info {
  margin-bottom: 20px;
}
.linkedin:hover i{
 transform: scale(1.1);
 box-shadow: 0 0 5px cyan;
}

.contact form input,
.contact form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.contact form button {
  background-color: #333;
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.footer{
  width: 100%;
  height: 50px;
  display: grid;
  place-content: center;
  color: white;
  font-size: 20px;
  background-color: #3905f5;
}
html{
  scroll-behavior: smooth;
}

/* Responsive Design */
@media (max-width: 768px) {
  .navbar div {
      flex-direction: column;
      gap: 20px;
      align-items: flex-start;
  }
  .about{
       text-align: center;
      /* padding: 20px; */
  }
  .profile-img{
      /* width: 150px;  */
      /* height: 150px;  */
      margin-top: 70px;
  }
  .skills-container {
      flex-direction: column;
  }

  .projects-container {
      flex-direction: column;
  }

  .project {
      width: 100%;
  }
}

@keyframes slideTop {
  0% {
    transform: translatey(100px);
     opacity: 0;
      }     
   100%{
     transform: translatey(0px);
     opacity: 1;
      }
 }
@keyframes slideBottom{
  0%{
      transform: translateX(100px);
      opacity: 0;
  }
  100%{
      transform:translateX(0px);
      opacity: 1;
}
}    
@keyframes slideRight{
  0%{
      transform: translatey(100px);
      opacity: 0;
  }
  100%{
      transform:translateY(0px);
      opacity: 1;
}
}
@keyframes slideLeft{
  0%{
      transform: translateX(100px);
      opacity: 0;
  }
  100%{
      transform:translateX(0px);
      opacity: 1;
}
}  